module.exports = "\r\n.navbar {\r\n  margin: auto;\r\n  height: auto;\r\n  position: absulote;\r\n  border: 1px;\r\n  overflow: visible;\r\n  z-index: 9999;\r\n\r\n}\r\n\r\n#navbarSupportedContent {\r\n  margin-bottom: 2px;\r\n  padding: 0;\r\n}\r\n\r\n.navbar-brand{\r\n  max-height: 150px;\r\n  max-width: 200px;\r\n  max-width: 30%;\r\n  overflow: visible;\r\n  padding: 0;\r\n  text-align: center;\r\n  position: relative;\r\n\r\n}\r\n\r\n.navbar-nav > li{\r\n  padding-left:10px;\r\n  padding-right:10px;\r\n}\r\n\r\nul {\r\n  list-style: none;\r\n  display: inline-flex;\r\n  margin-top: 16px;\r\n  text-align: center;\r\n\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n  color: black;\r\n\r\n}\r\n\r\n.primary-nav-item {\r\n  list-style: none;\r\n  display: inline-flex;\r\n  padding: 12px;\r\n\r\n}\r\n\r\n.dropbtn , .btn{\r\n\r\n  color: black;\r\n  padding: 12px;\r\n  font-size: 16px;\r\n  border: none;\r\n  cursor: pointer;\r\n  border-radius: 3px;\r\n}\r\n\r\n/* The container <div> - needed to position the dropdown content */\r\n\r\n.dropdown {\r\n\r\n  display: flexbox;\r\n\r\n}\r\n\r\n/* Dropdown Content (Hidden by Default) */\r\n\r\n.dropdown-content {\r\n  display: none;\r\n  position: absolute;\r\n  background-color: #f9f9f9;\r\n  min-width: 160px;\r\n  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\r\n  z-index: 1;\r\n}\r\n\r\n/* Links inside the dropdown */\r\n\r\n.dropdown-content a {\r\n  color: black;\r\n  position: relative;\r\n  padding: 12px 16px;\r\n  text-decoration: none;\r\n  display: block;\r\n}\r\n\r\n/* Change color of dropdown links on hover */\r\n\r\n.dropdown-content a:hover {background-color: #f1f1f1}\r\n\r\n/* Show the dropdown menu on hover */\r\n\r\n.dropdown:hover .dropdown-content{\r\n  display: block;\r\n}\r\n\r\n.primary-nav:hover .primary-nav-item {\r\n  display: block;\r\n\r\n}\r\n\r\n/* Change the background color of the dropdown button when the dropdown content is shown */\r\n\r\n.dropdown:hover .dropbtn {\r\n  background-color: #81aee2;\r\n}\r\n\r\n.btn:hover {\r\n  background-color: rgb(193, 230, 193);\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"